import React from 'react';
import { connect } from 'react-redux'
import Login from './Login'
import "./header.css";



const Content = (props) => {

    const sta = props.location.state;

    return (
        <div>
            {
                <div>
                    <p style={{
                        backgroundColor: 'rgba(0,0,0,0.4)',
                        color: 'white',
                        width: '40px',
                        height: '40px',
                        fontSize: '35px',
                        lineHeight: '40px',
                        textAlign: 'center',
                        borderRadius: '30px',
                        position: 'absolute',
                        top: '15px',
                        left: '15px'
                    }}
                        onClick={() => props.history.push('/Home')}
                    >
                        &lt;
                        </p>

                    {/* 商品 */}
                    <img src= {sta.img} 
                        style={{
                            width: '100%',
                        }}
                    />

                    {/* 标签 */}
                    <div className='bottombar'>

                        <div className='shop'
                            style={{
                                display: 'inline-block',
                                marginLeft: '18px'
                            }}>
                            <div className='iconfont icon-dianpu'
                                style={{ marginLeft: '5px' }}
                            ></div>
                            <div
                                style={{
                                    display: 'inline-block',
                                    height: '50px',
                                    paddingTop: '4px',
                                    marginLeft: '3px',
                                    marginTop: '10px'
                                }}>店铺</div>
                        </div>

                        <div className='kefu'
                            style={{
                                display: 'inline-block',
                                marginLeft: '8px'
                            }}>
                            <div className='iconfont icon-taobaokefu-01'
                                style={{ marginLeft: '7px' }}></div>
                            <div style={{
                                display: 'inline-block',
                                marginTop: '13px',
                                marginLeft: '5px'
                            }}>客服</div>
                        </div>

                        <div className='shoucang'
                            style={{
                                display: 'inline-block',
                                marginLeft: '8px',
                                marginTop: '12px'
                            }}>
                            <div className='iconfont icon-shoucang' id='shoucang'
                                onClick={(e) => {
                                    if (props.username) {
                                        props.dispatch({
                                            type: 'ADD_GOODS',
                                            value:props.location.state
                                        })

                                    } else if (!props.username) {
                                        props.history.push('/Login')
                                    }
                                    document.getElementById('shoucang').className = 'iconfont icon-shoucang1-copy'
                                }}
                                style={{ 
                                    display: 'inline-block', 
                                    marginLeft: '10px' 
                                    }}></div>
                            <div style={{ 
                                marginLeft: '8px', 
                                marginTop: '13px' 
                                }}>收藏</div>
                        </div>

                        <div style={{ marginTop: '-90px', marginLeft: '165px' }}>
                            <div style={{ 
                                display: 'inline-block', 
                                height: '60px', 
                                width: '100px', 
                                borderTopLeftRadius:'25px',
                                borderBottomLeftRadius:'25px',                                
                                backgroundColor: '#FF7F50', 
                                textAlign: 'center', 
                                fontSize: '14px', 
                                color: 'white', 
                                lineHeight: '60px', 
                                marginTop: '-10px' }}>加入购物车</div>
                            <div style={{ 
                                display: 'inline-block', 
                                height: '60px', 
                                width: '100px', 
                                borderTopRightRadius:'25px',
                                borderBottomRightRadius:'25px',
                                backgroundColor: '#FF4500', 
                                textAlign: 'center', 
                                fontSize: '14px', 
                                color: 'white', 
                                lineHeight: '60px' }}>立即购买</div>
                        </div>
                    </div>
                </div>
            }
        </div>

    )
}

const mapStateToProps = (state) => {
    console.log(state.loginreducer)
    return {
        username: state.loginreducer.username
    }
}

export default connect(mapStateToProps)(Content)