
import React,{useState} from 'react'
import {connect} from 'react-redux'
import Mytab from './Mytab'
import {list} from './actionreducer'


const Dingdan = (props)=>{
    

    return (
        <div>
            {/* <div>收藏列表</div> */}
            <div>
                {
                    props.shoucang.map((item,idx)=>(
                        <li key={idx}>
                            <img src={item.img} 
                            style={{width:'50%',
                            height:'50%'}}/>
                            <button onClick={()=>{
                                props.dispatch({
                                    type:'DEL_GOODS',
                                    index:idx
                                })
                            }}>删除</button>
                        </li>
                    ))
                }
            </div>

            <div className='bottom'>
                <Mytab />
            </div>
        </div>
        
    )
}

const mapStateToProps = (state) => {
    console.log(state.loginreducer)
    return {
        shoucang:state.shoucangreducer,
        username: state.loginreducer.username
    }
}

export default  connect(mapStateToProps)(Dingdan)