let todo = []

function shoucangreducer(state=todo,action){
    if(action.type=='ADD_GOODS'){
        console.log('reducer',action);
        return [...state,action.value]
    }
    if(action.type=='DEL_GOODS'){
        // action.index
        let arr = [...state];
        arr.splice(action.index,1);
        return arr;
    }
    return state;
}
export default shoucangreducer;