import React from 'react'
import { Carousel, WingBlank } from 'antd-mobile';

//轮播图
class Lunbotu extends React.Component {
      state = {
        data: ['1', '2', '3'],
        imgHeight: '200px',
      }
      componentDidMount() {
        // simulate img loading
        setTimeout(() => {
          this.setState({
            data: [
              '//gw.alicdn.com/imgextra/i1/93/O1CN01A8FWoi1CYdStUW8Dh_!!93-0-lubanu.jpg',
               'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg', 
               '//gw.alicdn.com/imgextra/i4/194/O1CN01QKLoBZ1DItT3AoAc0_!!194-0-lubanu.jpg'],
          });
        }, 100);
      }
      render() {
        return (
          
            <Carousel
              autoplay={true}
              infinite
              beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
              afterChange={index => console.log('slide to', index)}
            >
              {this.state.data.map(val => (
                <a
                  key={val}
                  href="http://www.alipay.com"
                  style={{ display: 'inline-block', width: 'auto', height: this.state.imgHeight, }}
                >
                  <img
                    src={`${val}`}
                    alt=""
                    style={{ width: '100%', verticalAlign: 'top', }}
                    onLoad={() => {
                      window.dispatchEvent(new Event('resize'));
                      this.setState({ imgHeight: 'auto' });
                    }}
                  />
                </a>
              ))}
            </Carousel>
        );
      }
}

export default Lunbotu