import React,{useEffect, useState} from 'react';
import './header.css'
import Lunbotu from './Lunbotu'
import Mytab from './Mytab';
import { connect } from 'react-redux'
import {list} from './actionreducer'


// iconfont.cn官网搜索图标下载到本地，
// 将解压后的文件全部放public文件夹中
// 在index.html中通过link引入iconfont.class
// 使用时，<i className='iconfont icon-图标名字'></i> 

//首页

const Home = (props) => {
    useEffect(()=>{
        props.dispatch(list());
    },[])

    return (
        <div>
            <div className='header'>
                <button type='text'
                    className='searchBar'
                    onClick={() => props.history.push('/searchBar')}
                    style={{ color: 'white' }}
                >
                    寻找宝贝店铺
                 </button>
            </div>

            {/* 轮播图 */}
            <div className='lunbotu'>
                <Lunbotu />
            </div>

            {/* 分隔符 */}
            <div className='like'>
                --你可能还喜欢--
            </div>

            {/* 商品列表 */}

            <div className='content'>
                {
                    props.lists.map((item)=>
                    <li className='contentList'
                    onClick={()=>props.history.push({pathname:'/Content',state:item})}>
                        <img src={item.img}/>
                        <span className='price'>￥{item.price}</span><span>{item.title}</span>
                        </li>)
                }
            </div>

            <div style={{ height: '50px', }}></div>
            <div className='bottom'>
                <Mytab />
            </div>
        </div>
    )

}

const mapStateToProps = (state) => {
    console.log(state.listreducer)
    return {
        lists: state.listreducer
    }
}

export default connect(mapStateToProps)(Home)
